h1, h2 {
  font-size: var(--fs-500);
}

h2 {
  padding-top: 1rem;
}

.temp-low {
  color: hsl( var(--clr-gray-blue-800) );
}

.flex {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
}

.App {
  min-height: 100vh;
  max-width: 850px;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 1rem auto;
}

.left, .right {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.right {
  margin-top: 1rem;
}

.card {
  border-radius: 6px;
  background-color: hsl( var(--clr-white) );
  width: 95%;
  max-width: 400px;
  box-shadow: 0 0 12px 0 rgba(0,0,0,.2);
}

.card + .card {
  margin-top: 1rem;
}


@media screen and (min-width: 50rem) {
  :root {
    --fs-700: 2.5rem;
    --fs-500: 1.75rem;
  }

  .App {
    margin: 0 auto;
    flex-direction: row;
  }

  .right {
    margin-top: 0;
  }
}
